import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { motion } from 'framer-motion';
import { useState } from 'react';
import { Axios, db } from '../Firebase/firebaseConfig.js';
import SocialFollow from "../SocialFollow";

const pageTransition = {
  in: {
    opacity: 1,
    y: 0
  },
  out: {
    opacity: 0,
    y: "-100%"
  }
}

const Contact = (props) => {

  const [formData, setFormData] = useState({})

  const updateInput = e => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    })
  }
  const handleSubmit = event => {
    event.preventDefault()
    sendEmail()
    setFormData({
      name: '',
      email: '',
      message: '',
    })
  }
  const sendEmail = () => {
    Axios.post(
      'https://us-central1-portfolio-1ca0b.cloudfunctions.net/submit',
      formData
    )
      .then(res => {
        db.collection('emails').add({
          name: formData.name,
          email: formData.email,
          message: formData.message,
          time: new Date(),
        })
      })
      .catch(error => {
        console.log(error)
      })
  }

  return (
    <motion.div initial="out" animate="in" exit="out" variants={ pageTransition } className="card col-sm-7 text-center container" id="mainContent">
      <div className= "card-body text-dark col ">
      <form onSubmit={handleSubmit}>
        <input
          type="text"
          name="name"
          placeholder="Name"
          onChange={updateInput}
          value={formData.name || ''}
        />
        <input
          type="email"
          name="email"
          placeholder="Email"
          onChange={updateInput}
          value={formData.email || ''}
        />
        <textarea
          type="text"
          name="message"
          placeholder="Message"
          onChange={updateInput}
          value={formData.message || ''}
        ></textarea>
        <button type="submit">Submit</button>
      </form>
      <br></br>
      <SocialFollow />
      </div>
    </motion.div>
  );
};

export default Contact;
